<template>
	<div id="creditshop-goods">
		<public-header title="兑换商品"></public-header>
		<div class="content">
			<van-search
				v-model="key"
				show-action
				background="#f5f5f5"
				placeholder="请输入关键字"
			>
				<div slot="action" @click="onSearch">搜索</div>
			</van-search>
			<div class="goods-group">
				<van-list
					v-model="goods.loading"
					:finished="goods.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!goods.empty"
				>
					<router-link :to="util.getUrl({path: '/pages/creditshop/goodsDetail', query: {id: item.id}})" v-for="(item, index) in goods.data" :key="index">
						<div class="goods-item">
							<div>
								<div class="image">
									<img :src="item.thumb" alt="">
								</div>
								<div class="detail">
									<div class="name">
										<span class="subtext bg-danger" v-if="item.type == 'goods'">商品</span>
										<span class="subtext bg-danger" v-else-if="item.type == 'credit2'">余额</span>
										<span class="subtext bg-danger" v-else-if="item.type == 'redpacket'">红包</span>
										{{item.title}}
									</div>
									<div class="price">
										<div class="price-item">
											<span class="num">{{item.use_credit1}}</span>
											<span class="text">积分</span>
											<template v-if="item.use_credit2 > 0">
												+
												<span class="num">¥{{item.use_credit2}}</span>
											</template>
										</div>
										<div class="exchange">兑换</div>
									</div>
								</div>
							</div>
						</div>
					</router-link>
					<div style="clear: both"></div>
					<load type="loaded" text="我是有底线的" v-if="goods.finished"></load>
				</van-list>
				<load type="loaded" text="暂无符合条件的商品" v-else></load>
			</div>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import Load from '@/components/load'
	export default {
		data() {
			return {
				category_id: Number,
				goods: {
					page: 1,
					psize: 4,
					title: '',
					loading: false,
					finished: false,
					empty: false,
					data: []
				},
				key: ''
			}
		},
		components: {
			PublicHeader,
			Load
		},
		methods: {
			onLoad(force) {
				if(force) {
					this.goods = {
						page: 1,
						psize: 4,
						title: this.key,
						loading: true,
						finished: false,
						empty: false,
						data: []
					};
				}
				if(this.goods.finished) {
					return false;
				}
				this.util.request({
					url: 'creditshop/index/goods',
					data: {
						page: this.goods.page,
						psize: this.goods.psize,
						category_id: this.category_id,
						title: this.goods.title
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message);
						return false;
					}
					result = result.message.goods;
					this.goods.data = [...this.goods.data, ...result];
					if(result.length < this.goods.psize ) {
						this.goods.finished = true;
						if(!this.goods.data.length) {
							this.goods.empty = true;
						}
					}
					this.goods.loading = false;
					this.goods.page++;
				})
			},
			onSearch() {
				if(this.key) {
					this.onLoad(true);
				}
			}
		},
		created() {
			if(this.$route.query.category_id) {
				this.category_id = this.$route.query.category_id;
			} else {
				this.util.$toast('参数错误')
				return false;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#creditshop-goods .content{
		bottom: 0;
		background: #f9f9f9;
	}
	#creditshop-goods .content #load .loaded .loaded-tips{
		background: #f5f5f5;
	}
	#creditshop-goods .content .van-search__action{
		color:#5f646e;
		padding: 0 10px;
	}
	#creditshop-goods .content .van-search .van-icon-clear{
		display: none;
	}
	#creditshop-goods .content .goods-group{
		padding:4px;
		background-color: #f9f9f9;
	}
	#creditshop-goods .content .goods-group a{
		color:#ff2d4b;
		overflow: hidden;
	}
	#creditshop-goods .content .goods-group a .goods-item{
		width:50%;
		float:left;
		border-bottom:0;
		display:block;
		background-color: #fff;
	}
	#creditshop-goods .content .goods-group a .goods-item>div{
		padding: 5px;
	}
	#creditshop-goods .content .goods-group a .goods-item .image{
		width:100%;
		height:191px;
		overflow:hidden;
		margin:0;
		background-position:center;
		background-repeat:no-repeat;
		background-size:cover;
	}
	#creditshop-goods .content .goods-group a .goods-item .image img{
		border: none;
		width: 100%;
		height: 100%;
	}
	#creditshop-goods .content .goods-group a .goods-item .detail{
		padding:11px;
		overflow:hidden;
		-webkit-box-flex:1;
		flex:1;
		background:#fff;
	}
	#creditshop-goods .content .goods-group a .goods-item .detail .name{
		height:37px;
		overflow:hidden;
		text-overflow:ellipsis;
		display:-webkit-box;
		-webkit-line-clamp:2;
		-webkit-box-orient:vertical;
		font-size:14px;
		line-height:19px;
	}
	#creditshop-goods .content .goods-group a .goods-item .detail .name .subtext{
		line-height:17px;
		border-radius:4px;
		display:inline-block;
		font-size:13px;
		padding:0 4px;
	}
	#creditshop-goods .content .goods-group a .goods-item .detail .price{
		position:relative;
		-webkit-box-align:center;
		align-items:center;
		font-size:15px;
		margin-top:6px;
		overflow: hidden;
	}
	#creditshop-goods .content .goods-group a .goods-item .detail .price .price-item{
		font-size:12px;
		float:left;
		text-overflow:ellipsis;
		white-space:nowrap;
		overflow:hidden;
		width:76%;
		color:#ff2d4b;
		line-height: 21px;
	}
	#creditshop-goods .content .goods-group a .goods-item .detail .price .price-item .num{
		font-size:17px;
		font-weight:bold;
	}
	#creditshop-goods .content .goods-group a .goods-item .detail .price .price-item .text{
		color: #999;
	}
	#creditshop-goods .content .goods-group a .goods-item .detail .price .exchange{
		float:right;
		background:#ff2d4b;
		line-height:22px;
		color:#fff;
		border-radius:5px;
		font-size:13px;
		padding:0 4px;
	}
</style>


